import { coerceBooleanProperty, coerceNumberProperty } from '@angular/cdk/coercion';
import { Component, Input, ChangeDetectionStrategy, ChangeDetectorRef, forwardRef, Inject } from '@angular/core';
import { NxStepComponent, NxProgressStepperDirective } from './../progress-stepper.component';
import { ErrorStateMatcher } from '@allianz/ngx-ndbx/utils';

/** @docs-private */
@Component({
  selector: 'nx-multi-step-item',
  templateUrl: './multi-step-item.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
  styleUrls: ['./multi-step-item.component.scss']
})
export class NxMultiStepItemComponent extends NxStepComponent {
  /** The state of the step. */
  @Input() state: string;

  /** The label of the step. */
  @Input() label: string;

  /** Sets the index of the selected step. */
  @Input()
  get index(): number { return this._index; }
  set index(value: number) { this._index = coerceNumberProperty(value); }
  private _index: number;

  /** Sets the selected step. */
  @Input()
  get selected(): boolean { return this._selected; }
  set selected(value: boolean) { this._selected = coerceBooleanProperty(value); }
  private _selected: boolean;

  /** Sets the active step. */
  @Input()
  get active(): boolean { return this._active; }
  set active(value: boolean) { this._active = coerceBooleanProperty(value); }
  private _active: boolean;

  /** Sets the last step. */
  @Input()
  get last(): boolean { return this._last; }
  set last(value: boolean) { this._last = coerceBooleanProperty(value); }
  private _last: boolean;

  /** Sets the first step. */
  @Input()
  get first(): boolean { return this._first; }
  set first(value: boolean) { this._first = coerceBooleanProperty(value); }
  private _first: boolean;

  /** Whether the step is completed or not. */
  @Input()
  set stepCompleted(value: boolean) {
    this._completed = coerceBooleanProperty(value);
    this._changeDetectorRef.markForCheck();
   }
  get stepCompleted(): boolean { return this._completed; }
  private _completed: boolean = false;

  constructor(private _changeDetectorRef: ChangeDetectorRef,
    @Inject(forwardRef(() => NxProgressStepperDirective)) stepper: NxProgressStepperDirective,
     _errorStateMatcher: ErrorStateMatcher) {
    super(stepper, _errorStateMatcher);
  }

}
